@keyframes example1 {
    0% {
        width: 0;
        opacity: 1; }

    2% {
        width: 0;
        opacity: 1; }

    50% {
        width: 100%;
        opacity: 1; }

    50.1% {
        width: 100%;
        opacity: 0; }

    100% {
        width: 0;
        opacity: 0; } }

@keyframes example2 {
    0% {
        width: 0;
        opacity: 0; }

    50% {
        width: 0;
        opacity: 1; }

    50.1% {
        width: 100%;
        opacity: 1; }

    52% {
        width: 100%;
        opacity: 1; }

    100% {
        width: 0;
        opacity: 1; } }

#preloader {
    position: fixed;
    width: 100%;
    height: 100%;
    background-color: $color_white;
    z-index: 9999;
    opacity: 1;
    transition: opacity .8s linear;
    &.fadeout {
      opacity: 0; }
    &.hide {
      display: none; }
    .blocks {
        position: absolute;
        height: 20px;
        width: 200px;
        z-index: 3;
        left: 50%;
        top: 50%;
        transform: translateX(-50%) translateY(-50%); }

    .blocks > div {
        position: absolute;
        top: 0;
        left: 0;
        width: 0;
        height: 100%;
        background-color: #1e2023;
        animation-name: example1;
        animation-duration: 2s;
        animation-timing-function: cubic-bezier(0.86, 0, 0.07, 1);
        animation-iteration-count: infinite; }

    .blocks > div:last-child {
        right: 0;
        left: auto;
        animation-name: example2; } }
