@keyframes example1 {
  0% {
    width: 0;
    opacity: 1;
  }
  2% {
    width: 0;
    opacity: 1;
  }
  50% {
    width: 100%;
    opacity: 1;
  }
  50.1% {
    width: 100%;
    opacity: 0;
  }
  100% {
    width: 0;
    opacity: 0;
  }
}

@keyframes example2 {
  0% {
    width: 0;
    opacity: 0;
  }
  50% {
    width: 0;
    opacity: 1;
  }
  50.1% {
    width: 100%;
    opacity: 1;
  }
  52% {
