  width: 200px;
  z-index: 3;
  left: 50%;
  top: 50%;
  transform: translateX(-50%) translateY(-50%);
}

#preloader .blocks > div {
  position: absolute;
  top: 0;
  left: 0;
  width: 0;
  height: 100%;
