.scroll-down-dude {
  margin: 20px auto 0;
  width: 20px;
}

.scroll-down-dude:before, .scroll-down-dude:after {
  content: '';
  display: block;
  width: 12px;
  height: 12px;
  transform: rotate(45deg);
  border-bottom: 2px solid #1db7d2;
  border-right: 2px solid #1db7d2;
}

.scroll-down-dude:before {
  animation: down-arrow-before 2.6s cubic-bezier(0.77, 0, 0.175, 1) infinite;
}

.scroll-down-dude:after {
  animation: down-arrow-after 2.6s cubic-bezier(0.77, 0, 0.175, 1) infinite;
}

@keyframes down-arrow-before {
  50% {
    transform: rotate(45deg) translate(70%, 70%);
  }
  100% {
    transform: rotate(45deg) translate(70%, 70%);
  }
}

@keyframes down-arrow-after {
  50% {
    transform: rotate(45deg) translate(110%, 110%);
    opacity: 0;
  }
  51% {
    transform: rotate(45deg) translate(-130%, -130%);
  }
  100% {
    transform: rotate(45deg) translate(-70%, -70%);
    opacity: 1;
  }
}

/* arrow */
.angle {
  position: absolute;
  width: 10px;
  height: 10px;
  border-right: 2px solid #1bb4d0;
  border-top: 2px solid #1bb4d0;
  transform: translate3d(-50%, -2px, 0) rotate(135deg);
  transition: all 0.3s cubic-bezier(0.7, 0, 0.3, 1);
}

.angle-white {
  position: absolute;
  width: 10px;
  height: 10px;
  border-right: 2px solid #fff;
  border-top: 2px solid #fff;
  transform: translate3d(-50%, -2px, 0) rotate(135deg);
  transition: all 0.3s cubic-bezier(0.7, 0, 0.3, 1);
}

.angle--right {
  width: 10px;
  height: 10px;
  transform: translate3d(-20%, -3px, 0) rotate(45deg);
  transition: none;
  margin-left: 8px;
  margin-top: 13px;
}

@media only screen and (max-width: 1399px) {
  .angle--right {
    margin-left: 8px;
    margin-top: 10px;
  }
}

